<template>
  <div id="app">
    <ul>
      <li v-for="area in areas">{{area.name}}</li>
    </ul>
    <pre>
      {{areas}}
    </pre>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      areas:[]
    }
  },
  mounted(){
    fetch('/api/areas')
    .then(res=>res.json())
    .then(data=>this.areas=data)
  }
}
</script>

<style lang="scss">

</style>



